
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font: inherit;
	vertical-align: baseline;
}

body{
  width: 100%;
  height: 100%;
  background-image: url(./images/bg-intro-desktop.png);
  background-color:  hsl(257, 40%, 49%) ;
  font-family: 'Open Sans' sans-serif;
  font-weight: 400;
  color: #ffff;
  display: grid;
  grid-template-rows: 35px 125px 3fr 150px 10px;
  grid-template-columns: 35px 2fr 2fr 10px ;
  gap: 20px;
}

.top{
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 3;
}

div.middle{
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 4;

}

.about-content{
  grid-column-start:3 ;
  grid-column-end:4 ;
  grid-row-start: 3;
  grid-row-end: 4;
}
.social-media{
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 4;
  grid-row-end: 5;  
}



img.social-media{
  height: 45px;
  width: 45px;
  border-radius: 50px;
  color: white;
  margin: 10px;
  padding: 6px;
  border: 2px solid white;
  position: relative;
  left:55%;
  top: 45%;
}

img#mockup{
  height: 450px;
  width: 600px;
  display: flex;
  flex-direction: column;
  align-self: flex-end;  
}

h2.about-content{
  font-weight:600;
  font-family: 'Poppins', sans-serif;
  font-size: 50px;
  padding: 15px 5px 15px 8px;  
}

p{
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 30px;
  margin: 10px 15px;
  text-align:left;
  line-height: 1.6;   
}

#register{
  font-size:27px ;
  background-color: whitesmoke;
  padding: .75em 2em;
  border-radius: 30px;
  color: hsl(257,40%,49%) ;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  position: relative;
  left: 8px;
  top: 15px;
}

img.social-media{
  height: 45px;
  width: 45px;
  border-radius: 50px;
  color: white;
  margin: 10px;
  padding: 6px;
  border: 2px solid white;
  position: relative;
  left:55%;
  top: 45%;  
}

